  position: absolute;
  height: auto;
  width: 350px;
  right: 0;
  top: 100vh;
  scroll-snap-align: start;
}
.macosChromeGuideIMG-2 {
  position: absolute;
  height: auto;
  width: 550px;
  left: calc(50% - 275px);
  top: calc(100vh + 400px);
  scroll-snap-align: start;
}
.macosChromeGuideIMG-3 {
  position: absolute;
  height: auto;
  width: 550px;
  left: calc(50% - 275px);
  top: calc(100vh + 892.85px);
  scroll-snap-align: start;
}

/* ios */
.iosSafariGuideIMG-1 {
  position: absolute;
