  .Table_Data.open {
    color: #1f1f1f;
    background: #d1d1d1;
    height: 4rem;
    margin-bottom: 5px;
    border-radius: 0.9rem;
    padding: 0.5rem 1rem 0.5rem 1rem;
    -webkit-border-radius: 0.9rem;
    -moz-border-radius: 0.9rem;
    -ms-border-radius: 0.9rem;
    -o-border-radius: 0.9rem;
  }
  .dark .Table_Data.open {
    color: #efefef;
    background-color: #262626;
  }
  .Table_Data.Double {
    justify-content: center;
    height: calc(4.8rem + 5px);
  }
  .Table_Data.Double.open {
    height: calc(8rem + 5px);
  }
  .Table_Data.Triple {
    justify-content: center;
    height: calc(7.2rem + 10px);
  }
  .Table_Data.Triple.open {
    height: calc(12rem + 10px);
  }
  .Table_Data.AllDay {
    justify-content: center;
    height: calc(19.2rem + 35px);
  }
  .Table_Data.AllDay.open {
    height: calc(32rem + 35px);
  }
  .Table_Data:hover {
    background: #d5d5d5;
  }
  .dark .Table_Data:hover {
    background: #323232;
  }
  .Table_Data span {
    color: #8a8a8a00;
    font-size: 0.7rem;
    font-weight: 200;
    letter-spacing: 0;
    line-height: 0.01;
    transition: 0.25s;
    -webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -ms-transition: 0.25s;
    -o-transition: 0.25s;
  }
  .Table_Data span.open {
    display: block;
    color: #808080;
    letter-spacing: 2px;
    line-height: 1.2;
  }
  .Table_Data.Vacances {
    background: linear-gradient(115deg, #0c770c, #0d527d);
    transition: 0.25s;
    -webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -ms-transition: 0.25s;
    -o-transition: 0.25s;
  }
  .Vacances:hover {
    background: linear-gradient(115deg, #0e890e, #0f5e8f);
  }
  .classchange {
    color: #efefef !important;
    background: linear-gradient(135deg, #9d1b1b, #9e6720) !important;
  }
  .dark .classchange {
    background: linear-gradient(135deg, #770c0c, #7d4c0d);
  }
  .classchange:hover {
    background: linear-gradient(135deg, #b22424, #b3782b) !important;
  }
  .dark .classchange:hover {
    background: linear-gradient(135deg, #891010, #915b13);
  }
  .classchange span.open {
    color: #cfcfcf;
  }
}
@media (min-height: 783px) {
  #courseSchedule {
    scroll-snap-type: y mandatory;
    scroll-padding-top: 2rem;
  }
  #CSView > h1 {
    scroll-snap-align: start;
  }
}

