/* === Setting ==================================================== */
#Setting {
  z-index: 6000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: -100vh;
  left: 0;
  background: #e3e3e3;
  transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
}
#Setting.dark {
  background: #1c1c1c;
}
#Setting.open {
  top: 0;
  transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
}
nav.settingOpen {
  top: 100vh;
}
footer.settingOpen {
  bottom: -68px;
}
main.settingOpen {
  top: 30vh;
}
#menu.settingOpen {
  bottom: -15vh;
}

#closeSetting {
  z-index: 6001;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: -28px;
  right: 18px;
  width: 10px;
  height: 10px;
  background: #da3939;
  color: #ffe3e3;
  opacity: 0;
  font-size: 18px;
  border-radius: 27px;
  -webkit-border-radius: 27px;
  -moz-border-radius: 27px;
  -ms-border-radius: 27px;
  -o-border-radius: 27px;
  transition: 0.85s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.85s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.85s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.85s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.85s cubic-bezier(0.5, 0, 0.3, 1);
  transform: rotate(-360deg);
  -webkit-transform: rotate(-360deg);
  -moz-transform: rotate(-360deg);
  -ms-transform: rotate(-360deg);
  -o-transform: rotate(-360deg);
  pointer-events: none;
}
#closeSetting.open {
  bottom: 24px;
  right: 24px;
  width: 54px;
  height: 54px;
  font-size: 28px;
  opacity: 1;
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  -moz-transform: rotate(360deg);
  -ms-transform: rotate(360deg);
  -o-transform: rotate(360deg);
  pointer-events: all;
}

